import React, {useEffect, useState} from "react";
import {useLocation, useNavigate, useParams} from "react-router-dom";
import {useAuth} from "../../config/AuthContext";
import useWorkData from "../utils/WorkData";
import createAxiosInstance from "../../config/api";
import workDataDefault from "../utils/WorkDataDefault";

function WorkScheduleDashboard (){
    const { username } = useAuth();
    const { date } = useParams();
    const year = new Date(date).getFullYear();
    const month = new Date(date).getMonth()+ 1;
    const navigate = useNavigate();
    const [item, setItem] = useState({});
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState("")
    const [isEditing, setIsEditing] = useState(false);
    const workData = useWorkData(year, month); // ✅ 최상위에서 호출
    const [editedItem, setEditedItem] = useState({});
    const data = workDataDefault();
    const [defaultItem] = useState({
        id: "",
        checkInDate: date,
        checkInTime: "",
        checkOutDate: date,
        checkOutTime: "",
        breakTimeIn: "",
        breakTimeOut: "",
        workType: "",
        workPosition: "",
        workLocation: "",
        memo: "사유를 작성해 주세요"
        });

    useEffect(() => {
                if (!date || Object.keys(workData).length === 0) return;
                if (workData[date]) {
                    setItem(workData[date]);
                    setEditedItem(workData[date]);
                } else {
                    setIsEditing(true);
                    const newDefaultItem = {
                        ...defaultItem,
                        checkInTime: data.checkInTime,
                        checkOutTime: data.checkOutTime,
                        breakTimeIn: data.breakTimeIn,
                        breakTimeOut: data.breakTimeOut,
                        workLocation: data.workLocation,
                    };
                    setItem(null);
                    setEditedItem(newDefaultItem);
                }
                setLoading(false);
        }, [workData, date, defaultItem, data]);

    function handleClickBack() {
        navigate("/workSchedule/list");
    }
    const handleEditClick = () => {
        setIsEditing(true);
    };
    function handleCancelClick() {
        if(!item || item.isEmpty){
            navigate("/workSchedule/list");
        } else {
            setIsEditing(false);
            setEditedItem(item);
        }
    }
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setEditedItem((prevItem) => ({
            ...prevItem,
            [name]: value,
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault(); // 폼 제출 방지
        const confirmSave = window.confirm("해당 근무표 정보를 저장하시겠습니까?");
        if (!confirmSave) {
            return;
        } else {
            try {
                console.log("editedItem", editedItem);
                const axiosInstance = createAxiosInstance(); // 인스턴스 생성
                const response = await axiosInstance.post(
                    "/workSchedule/save",
                    editedItem
                );
                setItem(response.data);
                setIsEditing(false);
                window.alert("근무표 정보를 수정하였습니다");
            } catch (err) {
                setError(err.response?.status === 404 ? "입력된 값을 다시 한번 확인해 주세요" : "근무표 정보를 수정하는 데 실패했습니다.");
            }
        }
    };

    const handleDeleteClick = async (e) =>{
        e.preventDefault();
        const confirmDelete = window.confirm("해당 근무표 정보를 삭제하시겠습니까?");
        if (!confirmDelete) {
            return;
        } else {
            try{
                const axiosInstance = createAxiosInstance(); // 인스턴스 생성
                await axiosInstance.delete(`/workSchedule/delete/${editedItem.id}`);
                window.alert("근무표 정보를 삭제하였습니다");
                navigate("/workSchedule/list");
            }catch (err){

            }
        }
    };

    if (loading) return <div>로딩 중...</div>;
    if (error) return <div>{error}</div>;



    return (
        <div className="detail-container mt-4">

            <div className="card">
                <form onSubmit={handleSubmit}>
                <div className="card-header">
                    <h3>근무표 상세 페이지</h3>
                </div>
                <div className="card-body">
                    {isEditing ? (
                        <>
                            <div>
                                <input
                                    className="form-control mb-2"
                                    name="id"
                                    value={editedItem?.id || ""}
                                    onChange={handleInputChange}
                                    disabled
                                    hidden
                                />
                            </div>
                            <div className="form-group">
                                <label className="label">출근 날짜 / 시간</label>
                                <div className="d-flex">
                                    <input
                                        name="checkInDate"
                                        type="date"
                                        className="input"
                                        value={editedItem.checkInDate}
                                        onChange={handleInputChange}
                                    />
                                    <input
                                        name="checkInTime"
                                        type="time"
                                        className="input"
                                        value={editedItem.checkInTime}
                                        onChange={handleInputChange}
                                    />
                                </div>
                            </div>
                            <div className="form-group">
                                <label className="label">퇴근 날짜 / 시간</label>
                                <div className="d-flex">
                                    <input
                                        name="checkOutDate"
                                        type="date"
                                        className="input"
                                        value={editedItem.checkOutDate}
                                        onChange={handleInputChange}
                                    />
                                    <input
                                        name="checkOutTime"
                                        type="time"
                                        className="input"
                                        value={editedItem.checkOutTime}
                                        onChange={handleInputChange}
                                    />
                                </div>
                            </div>
                            <div className="form-group">
                                <label className="label">휴게시간</label>
                                <div className="d-flex">
                                    <input
                                        name="breakTimeIn"
                                        type="time"
                                        className="input"
                                        value={editedItem.breakTimeIn}
                                        onChange={handleInputChange}
                                    />
                                    <span> ~ </span>
                                    <input
                                        name="breakTimeOut"
                                        type="time"
                                        className="input"
                                        value={editedItem.breakTimeOut}
                                        onChange={handleInputChange}
                                    />
                                </div>
                            </div>
                            <div className="form-group">
                                <label className="label">근무 유형</label>
                                <input
                                    name="workType"
                                    type="text"
                                    className="input"
                                    value={editedItem.workType}
                                    onChange={handleInputChange}
                                />
                            </div>
                            <div className="form-group">
                                <label className="label">근태 유형</label>
                                <input
                                    name="workPosition"
                                    type="text"
                                    className="input"
                                    value={editedItem.workPosition}
                                    onChange={handleInputChange}
                                />
                            </div>
                            <div className="form-group">
                                <label className="label">근무지</label>
                                <input
                                    name="workLocation"
                                    type="text"
                                    className="input"
                                    value={editedItem.workLocation}
                                    onChange={handleInputChange}
                                    disabled
                                />
                            </div>
                            <div className="form-group">
                                <label>사유</label>
                                <input
                                    name="memo"
                                    type="text"
                                    className="input"
                                    value={editedItem.memo}
                                    onChange={handleInputChange}
                                />
                            </div>
                        </>
                        ) : (
                            <>
                                <div className="form-group">
                                    <label className="label">출퇴근 시간</label>
                                    <div className="d-flex">
                                        <input
                                            type="time"
                                            className="input"
                                            value={item?.checkInTime || defaultItem.checkInTime}
                                            readOnly
                                        />
                                        <span> ~ </span>
                                        <input
                                            type="time"
                                            className="input"
                                            value={item?.checkOutTime || defaultItem.checkOutTime}
                                            readOnly
                                        />
                                    </div>
                                </div>

                                <div className="form-group">
                                    <label className="label">휴게시간</label>
                                    <div className="d-flex">
                                        <input
                                            type="time"
                                            className="input"
                                            value={item?.breakTimeIn || defaultItem.breakTimeIn}
                                            readOnly
                                        />
                                        <span className="text-gray-500 me-2 fs-5"> ~ </span>
                                        <input
                                            type="time"
                                            className="input"
                                            value={item?.breakTimeOut|| defaultItem.breakTimeOut}
                                            readOnly
                                        />
                                    </div>
                                </div>
                                <div className="form-group">
                                    <label className="label">근무 유형</label>
                                    <input
                                        type="text"
                                        className="input"
                                        value={item?.workType|| defaultItem.workType}
                                        readOnly
                                    />
                                </div>
                                <div className="form-group">
                                    <label className="label">근태 유형</label>
                                    <input
                                        type="text"
                                        className="input"
                                        value={item?.workPosition|| defaultItem.workPosition}
                                        readOnly
                                    />
                                </div>
                                <div className="form-group">
                                    <label className="label">근무지</label>
                                    <input
                                        type="text"
                                        className="input"
                                        value={item?.workLocation|| defaultItem.workLocation}
                                        readOnly
                                    />
                                </div>
                                    <div className="form-group">
                                        <label>사유</label>
                                        <input
                                            type="text"
                                            className="input"
                                            value={item?.memo|| defaultItem.checkInTime}
                                            readOnly
                                        />
                                    </div>
                            </>
                    )}
                </div>
                <div className="card-footer">
                    {isEditing ? (
                        <button
                            type="button"
                            className="btn btn-primary me-4"
                            onClick={handleCancelClick}
                        >
                            취소
                        </button>
                    ) : (
                        <button
                            type="button"
                            className="btn btn-primary me-3"
                            onClick={handleClickBack}
                        >
                            목록으로 돌아가기
                        </button>
                    )}

                    {isEditing ? (
                        <button
                            type="submit"
                            className="btn btn-secondary"
                        >
                            저장
                        </button>
                    ) : (<></>)}

                    {isEditing ? (<></>) : (
                        <button
                            type="button"
                            className="btn btn-secondary me-4"
                            onClick={handleEditClick}
                        >
                            수정하기
                        </button>
                    )}
                    {item && !isEditing ? (<button
                        type="button"
                        className="btn btn-danger "
                        onClick={handleDeleteClick}
                    >
                        삭제
                    </button>
                    ) : (<></>)}
                </div>
                </form>
            </div>
</div>
    );
}
export default WorkScheduleDashboard;