import { useState, useEffect } from "react";
import createAxiosInstance from "../../config/api";
import { useAuth } from "../../config/AuthContext";

const useWorkData = (year, month) => {
    const { username } = useAuth();
    const [workData, setWorkData] = useState({});
    let cachedHolidays = null; // 캐싱하여 여러 번 호출해도 다시 요청하지 않음

    useEffect(() => {
        if (cachedHolidays) {
            setWorkData(cachedHolidays); // 이미 로드된 데이터가 있다면 설정
            return;
        }

        const fetchData = async () => {
            try {
                const axiosInstance = createAxiosInstance();
                const response = await axiosInstance.get(
                    `/workSchedule/${username}/${year}/${month}`
                );

                const newWorkDataList = {};
                response.data.forEach((event) => {
                    const date = new Date(event.checkInDate);
                    const key = `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`;
                    newWorkDataList[key] = {
                        id: event.id || "",
                        checkInDate: event.checkInDate || "",
                        checkInTime: event.checkInTime || "",
                        checkOutDate: event.checkOutDate || "",
                        checkOutTime: event.checkOutTime || "",
                        isHoliday: event.isHoliday !== undefined ? event.isHoliday : "",
                        memo: event.memo || "",
                        breakTimeIn: event.breakTimeIn || "",
                        breakTimeOut: event.breakTimeOut || "",
                        workType: event.workType || "",
                        workLocation: event.workLocation || "",
                        workPosition: event.workPosition || "",
                    };
                });

                cachedHolidays = newWorkDataList; // 캐싱하여 중복 요청 방지
                setWorkData(newWorkDataList);
                console.log(newWorkDataList);
            } catch (err) {
                console.error("Error fetching workData" + err);
            }
        };

        fetchData();
    }, [year, month]);

    return workData;
};

export default useWorkData;
